.home-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(135deg, #1e3c72 0%, #2a5298 100%);
  color: white;
  text-align: center;
}

.content {
  max-width: 600px;
}

h1 {
  font-size: 2.5rem;
  margin: 0.5rem 0;
}

p {
  font-size: 1.25rem;
  margin: 1rem 0;
}

button {
  background-color: #1e90ff;
  color: white;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 1rem;
}

button:hover {
  background-color: #1c86ee;
}
